import { Component } from '@angular/core';
import { ViewController,IonicPage, MenuController, NavController, Platform } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { TranslateService } from '@ngx-translate/core';
import { MainPage } from '../../pages';

export interface Slide {
  title: string;
  description: string;
  image: string;
}

@IonicPage()
@Component({
  selector: 'page-tutorial',
  templateUrl: 'tutorial.html'
})
export class TutorialPage {
  slides: Slide[];
  showSkip = true;
  dir: string = 'ltr';
  estilomenu:string=''
  tiposus: any=[];
  rol:any;

//	@ViewChild('slides') slides: Slides;

  constructor( public viewCtrl: ViewController,public storage: Storage,public navCtrl: NavController, public menu: MenuController, translate: TranslateService, public platform: Platform) {
    this.dir = platform.dir();

    translate.get('APPX').subscribe((value) => {
      this.estilomenu=value
      if(value=='WebApp Usacenlinea')
      {

        translate.get(["TUTORIAL_SLIDE1_TITLE",
        "TUTORIAL_SLIDE1_DESCRIPTION",
        "TUTORIAL_SLIDE2_TITLE",
        "TUTORIAL_SLIDE2_DESCRIPTION",
        "TUTORIAL_SLIDE3_TITLE",
        "TUTORIAL_SLIDE3_DESCRIPTION",
      ]).subscribe(
        (values) => {
          console.log('Loaded values', values);
          this.slides = [
            {
              title: values.TUTORIAL_SLIDE1_TITLE,
              description: values.TUTORIAL_SLIDE1_DESCRIPTION,
              image: 'assets/img/ica-slidebox-img-1.png',
            },
            {
              title: values.TUTORIAL_SLIDE2_TITLE,
              description: values.TUTORIAL_SLIDE2_DESCRIPTION,
              image: 'assets/img/ica-slidebox-img-2.png',
            },
            {
              title: values.TUTORIAL_SLIDE3_TITLE,
              description: values.TUTORIAL_SLIDE3_DESCRIPTION,
              image: 'assets/img/ica-slidebox-img-3.png',
            }
          ];
        });

      }
      else
      {
        
    translate.get(["TUTORIAL_SLIDE1a_TITLE",
    "TUTORIAL_SLIDE1a_DESCRIPTION",
    "TUTORIAL_SLIDE2a_TITLE",
    "TUTORIAL_SLIDE2a_DESCRIPTION",
    "TUTORIAL_SLIDE3a_TITLE",
    "TUTORIAL_SLIDE3a_DESCRIPTION",
  ]).subscribe(
    (values) => {
      console.log('Loaded values', values);
      this.slides = [
        {
          title: values.TUTORIAL_SLIDE1_TITLE,
          description: values.TUTORIAL_SLIDE1_DESCRIPTION,
          image: 'assets/img/ica-slidebox-img-1.png',
        },
        {
          title: values.TUTORIAL_SLIDE2_TITLE,
          description: values.TUTORIAL_SLIDE2_DESCRIPTION,
          image: 'assets/img/ica-slidebox-img-2.png',
        },
        {
          title: values.TUTORIAL_SLIDE3_TITLE,
          description: values.TUTORIAL_SLIDE3_DESCRIPTION,
          image: 'assets/img/ica-slidebox-img-3.png',
        }
      ];
    });


      }
    });


  }

  startApp() {
    this.storage.set('hasSeenTutorial', 'false');
   
   // this.navCtrl.enable(true);

                
  if(this.estilomenu!='WebApp Usacenlinea')
  {
  //  this.rootPageParams = {uno: "Tab5Root", dos: "Tab5Root" }
    this.navCtrl.setRoot(MainPage,{uno: "Tab5Root", dos: "Tab5Root" }, { animate: true, direction: 'forward'  });
  }
  else
  {

    if(this.rol=='lluviaideas')
    {
     
 //   this.rootPageParams = {uno: "Tab4Root", dos: "Tab4Root" }
    this.navCtrl.setRoot(MainPage,{uno: "Tab4Root", dos: "Tab4Root" }, { animate: true, direction: 'forward'  });
    }
    else
    {
     
        
 //   this.rootPageParams = {uno: "Tab1Root", dos: "Tab3Root" }
    this.navCtrl.setRoot(MainPage,{uno: "Tab1Root", dos: "Tab3Root" }, { animate: true, direction: 'forward'  });
      
    
    }
  }


 
   // this.navCtrl.setRoot(MainPage, {uno: "Tab1Root", dos: "Tab3Root" }, { animate: true, direction: 'forward'  });
    //this.viewCtrl.dismiss();
  
   
    
  }

  onSlideChangeStart(slider) {
    this.showSkip = !slider.isEnd();
  }

  ionViewWillEnter() {
   // this.slides.update();
  }

  ionViewDidEnter() {
    // the root left menu should be disabled on the tutorial page

   // this.menu.enable(false);
  }

  ionViewWillLeave() {
    // enable the root left menu when leaving the tutorial page
    //this.menu.enable(false);
  }

  
  ionViewDidLoad() : void
  {

  this.storage.get('tiposus').then((value) => {
    this.tiposus = value.split(',');
   this.rol=this.tiposus[6]



});
}


}



